import React, { useState } from "react";
import CodeBlock from "./CodeBlock";  // CodeBlock bileenini dahil ediyoruz
import { fetchAIResponse } from "./openaiService"; // API entegrasyonu iin ekleme
import "./App.css";

function App() {
  const [messages, setMessages] = useState([]);
  const [userMessage, setUserMessage] = useState("");
  const [isTyping, setIsTyping] = useState(false);

  const handleSendMessage = async () => {
    if (!userMessage.trim()) return;

    const newMessages = [...messages, { role: "user", content: userMessage }];
    setMessages(newMessages);
    setUserMessage(""); // Giri alann temizle
    setIsTyping(true);

    try {
      const response = await fetchAIResponse(userMessage); // API ars
      setMessages([...newMessages, { role: "assistant", content: response }]);
    } catch (error) {
      setMessages([...newMessages, { role: "assistant", content: "Bir hata olutu." }]);
    } finally {
      setIsTyping(false);
    }
  };

  // Komutlar zel olarak ileme fonksiyonu
  const renderMessageContent = (content) => {
    // Eer ierik bir git komutu veya bash komutu ieriyorsa, ona gre stil uygula
    if (content.includes("git ")) {
      return <CodeBlock code={content} language="bash" className="git-command" />;
    } else if (content.includes("$")) {
      return <CodeBlock code={content} language="bash" className="bash-command" />;
    } else {
      return <p>{content}</p>;
    }
  };

  return (
    <div className="App">
      <header className="App-header">
        <h1>ChatGPT Benzeri Sohbet</h1>
        <div className="chat-container">
          <div className="messages">
            {messages.map((msg, index) => (
              <div
                key={index}
                className={`message ${msg.role === "user" ? "user" : "assistant"}`}
              >
                {renderMessageContent(msg.content)}
              </div>
            ))}
            {isTyping && <div className="typing-indicator">Yazyor...</div>}
          </div>
          <input
            placeholder="Bir mesaj yazn..."
            value={userMessage}
            onChange={(e) => setUserMessage(e.target.value)}
            rows="4"
          />
          <button onClick={handleSendMessage}>Gnder</button>
        </div>
      </header>
    </div>
  );
}

export default App;
